.card {
  position: relative;
  display: inline-block;
  height: 100%;
  z-index: 50;
  animation: draw-intro 0.25s ease-out;
}
.card h1 {
  color: white;
  position: absolute;
  top: 54.25%;
  font-size: 0.5em;
  text-align: center;
  font-family: "belwe_fsextrabold", serif;
  margin: 0 8%;
  width: 84%;
  max-width: 84%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card div.visuals .inhand-minion {
  width: 78%;
  top: 6%;
  left: 11%;
  clip-path: url(assets/clip-paths.svg#inhand-minion-clip);
  -webkit-clip-path: ellipse(36% 47% at 50% 50%);
}
.card div.visuals .inhand-spell {
  width: 70%;
  top: 11%;
  left: 14%;
  clip-path: url(assets/clip-paths.svg#inhand-spell-clip);
  -webkit-clip-path: inset(8% 0% 4% 0%);
}
.card div.visuals .inhand-weapon {
  width: 67%;
  top: 11%;
  left: 17%;
  clip-path: url(assets/clip-paths.svg#inhand-weapon-clip);
  -webkit-clip-path: circle(50% at 50% 50%);
}
.card div.visuals .inhand-base,
.card div.visuals .inhand-legendary,
.card div.visuals .inhand-mulligan {
  height: 100%;
}
.card div.visuals .inhand-mulligan {
  z-index: 55;
}
.card .description {
  position: absolute;
  font-family: "franklin_gothic_fsMdCn", sans-serif;
  font-size: 0.6em;
  margin: 0 16%;
  text-align: center;
  line-height: 1em;
  height: 26%;
  max-height: 26%;
  overflow: hidden;
  width: 65%;
  top: 64.5%;
  left: 0.5%;
}
.card .description p {
  width: 100%;
  position: absolute;
  margin: 0;
  padding: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.card .cost {
  top: 9.75%;
  left: 2.0%;
}
.card.card-spell .cost,
.card.card-hero-power .cost {
  left: 3.5%;
}
.card .stats .atk {
  left: 5%;
  top: 85%;
}
.card .stats .health,
.card .stats .durability {
  left: 76%;
  top: 85%;
}
.card .cost,
.card .atk,
.card .health,
.card .durability {
