}
.card:hover {
  z-index: 51;
}
.card:hover .inhand-mulligan {
  opacity: 0.5;
}
.card .created-by {
  position: relative;
  width: 90%;
  left: 5%;
  line-height: 1.5em;
  font-size: 0.6em;
  text-align: center;
  font-family: "belwe_fsextrabold", serif;
  color: white;
}
.card h1,
.card .created-by {
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
