  top: -50%;
}
.hero,
.weapon,
.heroPower,
.deck > figure {
  filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.8));
}
.card,
.minion {
  filter: drop-shadow(-3px 2px 2px rgba(0, 0, 0, 0.4));
}
.entity .mouse-over {
  display: block;
  position: absolute;
  z-index: 100;
  left: 100%;
}
.entity .mouse-over > .card {
  animation: none;
  z-index: 101;
}
/* premium class card filter colors, for use with red overlay image */
.druid-color {
  filter: hue-rotate(70deg) saturate(85%) brightness(94%);
}
.hunter-color {
  filter: hue-rotate(150deg) saturate(80%) brightness(100%);
}
.mage-color {
  filter: hue-rotate(220deg) saturate(40%) brightness(125%);
}
.paladin-color {
  filter: hue-rotate(55deg) saturate(120%) brightness(70%);
  opacity: 0.7;
}
.priest-color {
  visibility: hidden;
}
.rogue-color {
  filter: hue-rotate(104deg) saturate(0%) brightness(78%);
}
.shaman-color {
  filter: hue-rotate(220deg) saturate(50%) brightness(100%);
}
.warlock-color {
  filter: hue-rotate(306deg) saturate(75%) brightness(55%);
}
.warrior-color {
  filter: hue-rotate(41deg) saturate(52%) brightness(70%);
}
.neutral-color {
  filter: hue-rotate(160deg) saturate(0%) brightness(80%);
  opacity: 0.4;
}
.player {
  width: 100%;
  height: 50%;
  position: relative;
}
.player > section {
  height: 50%;
}
.player > section.tall {
  height: 55%;
}
.player > section.short {
  height: 45%;
}
.player > section > div,
.player > section > ul {
  height: 100%;
  margin: 0;
  padding: 0;
  display: inline-block;
  vertical-align: top;
}
.player > .choices,
.player > .played {
  display: block;
  position: absolute;
  margin: 0 0 0 25%;
  padding: 0;
}
.player > .choices {
  top: 0;
  left: 0;
  width: 75%;
  height: 100%;
  z-index: 100;
}
.player > .played {
  animation: played-intro 0.25s ease-out;
  top: -10%;
