  height: 57.5%;
  z-index: 99;
  margin-right: 10%;
}
.player.top > .played {
  top: 55%;
  right: 55%;
}
.player.top .heroPower .card {
  top: 0;
}
.player.top .weapon .card {
  top: 15%;
}
.player .heroPower .card,
.player .weapon .card {
  height: 135%;
  top: -50%;
}
.player .heroPower.hovering,
.player .weapon.hovering {
  z-index: 101;
}
.player .gameresult {
  position: absolute;
  top: 35%;
  left: 0;
  width: 100%;
  height: 100%;
}
.top.player .gameresult {
  top: 45%;
}
.player .hand {
  width: 80%;
}
.player .hand > * {
  flex-shrink: 1;
  min-width: 10%;
}
.player .equipment {
  width: 20%;
  padding: 10px;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.player .equipment > section.entities {
  max-width: 100%;
  height: 80%;
  display: flex;
}
.player .equipment > section.entities > section {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}
.player .equipment > section.entities .weapon,
.player .equipment > section.entities .heroPower {
  height: 50%;
}
.player .equipment > section.details {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.player .equipment > section.details .tray {
  display: flex;
  flex-direction: row;
  height: 1.4em;
  padding: 0.1em 0.6em;
  margin-left: 0.6em;
}
.player .equipment > section.details .tray > span {
  padding: 0 0.5em 0 0;
}
.player .equipment > section.details .name {
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 1.4em;
  padding: 0.1em 0.6em;
}
.player .field {
  width: 80%;
  overflow: visible;
}
.player .field > * {
  flex-shrink: 1;
  max-width: 14.28571429%;
}
.player .deck {
  width: 20%;
  align-items: center;
}
.player .deck > figure {
  height: 70%;
  width: 70%;
  position: relative;
  padding: 0;
  margin: -9% 0 0 0;
  text-align: center;
}
.player .deck > figure img {
  height: 100%;
}
.player .deck > figure figcaption {
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  font-size: 1.5em;
  position: absolute;
  bottom: 7%;
  width: 100%;
  text-align: center;
}
.top.player .deck > figure figcaption {
  top: 7%;
}
.player .hand,
.player .field,
.player .choices,
.player .deck {
  display: inline-flex;
  flex: 0 0 0;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  padding: 5px 0;
}
.entity-list {
  list-style-type: none;
}
.entity-list li {
  height: 100%;
  display: inline-block;
  margin: 0;
  padding: 0;
}
.joust-scrubber {
  height: 35px;
  display: flex;
  flex-direction: row;
  z-index: 1000;
  margin-top: 8px;
  user-select: none;
  position: relative;
  font-family: sans-serif;
}
.joust-scrubber > button,
.joust-scrubber > select,
.joust-scrubber > .joust-tooltipper {
  margin: 0 8px 0 0;
}
.joust-scrubber > button:last-child,
.joust-scrubber > select:last-child,
.joust-scrubber > .joust-tooltipper:last-child {
  margin-right: 0;
}
.joust-scrubber button:not(.joust-scrubber-width-auto) {
  width: 35px;
}
.joust-scrubber button.joust-scrubber-button-wide {
  width: 52.5px;
}
.joust-scrubber button,
.joust-scrubber select {
  color: black;
  font-size: 1.1em;
  height: 35px;
  background-color: white;
  border: solid 1px black;
  border-radius: 4px;
  cursor: pointer;
}
.joust-scrubber button:disabled,
.joust-scrubber select:disabled {
  color: gray;
  background-color: #ddd;
  border-color: gray;
  cursor: not-allowed;
}
.joust-scrubber-timeline {
  position: relative;
  flex: 1;
  background-color: #23426a;
  margin: 0 8px 0 0;
  height: 100%;
  width: 100%;
  border: solid 1px #3ea1d9;
  color: white;
  overflow: hidden;
  cursor: grab;
}
.joust-scrubber-timeline:not(.no-turns) {
  border-top: none;
  border-bottom: none;
}
.joust-scrubber-timeline.dragging {
  cursor: grabbing;
}
.joust-scrubber-timeline.disabled {
  cursor: not-allowed;
}
.joust-scrubber-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1002;
  border-right: solid 2px white;
  background-color: #081d29;
  mix-blend-mode: screen;
}
.joust-scrubber-progress.inverse {
  left: auto;
  right: 0;
  border-right: none;
  border-left: solid 2px white;
}
.no-turns .joust-scrubber-progress.inverse {
  border-left: none;
}
.no-turns .joust-scrubber-progress {
  border-right: none;
}
.joust-scrubber-turn {
  display: inline-block;
  z-index: 1001;
  height: 100%;
  border-top: solid 1px #3ea1d9;
  border-bottom: solid 1px #3ea1d9;
  vertical-align: top;
  padding: 1px 0 0 0;
  font-family: sans-serif;
  font-size: 0.8em;
}
.joust-scrubber-turn.mulligan {
  padding-top: 4px;
  padding-left: 2px;
  overflow: hidden;
}
.joust-scrubber-turn.top {
  border-bottom-color: #3ea1d9;
  border-bottom-width: 4px;
  padding-top: 4px;
}
.joust-scrubber-turn.bottom {
  border-top-color: #3ea1d9;
  border-top-width: 4px;
}
.joust-scrubber-turn.top + .joust-scrubber-turn.top,
.joust-scrubber-turn.bottom + .joust-scrubber-turn.bottom {
  border-left: #3ea1d9 dotted 1px;
}
.joust-tooltipper {
  position: relative;
}
.joust-tooltipper > .joust-tooltipper-tooltip {
  z-index: 1000;
  display: block;
  position: absolute;
  top: -32px;
  width: 0;
  left: 50%;
}
.joust-tooltipper > .joust-tooltipper-tooltip > span {
  text-align: center;
  font-size: 1em;
  display: inline-block;
  white-space: nowrap;
  font-family: sans-serif;
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 4px 8px;
  border: 1px rgba(255, 255, 255, 0.25) solid;
  border-radius: 4px;
  transform: translateX(-50%);
}
.joust-tooltipper > .joust-tooltipper-tooltip.left {
  left: 0;
}
.joust-tooltipper > .joust-tooltipper-tooltip.left > span {
  transform: none;
}
.joust-tooltipper > .joust-tooltipper-tooltip.right {
  left: auto;
  right: 0;
}
.joust-tooltipper > .joust-tooltipper-tooltip.right > span {
  transform: translateX(-100%);
}
.joust-tooltipper > .joust-tooltipper-tooltip:hover {
  opacity: 0.3;
}
.joust-scrubber-settings {
