}
.joust-scrubber-settings section label span {
  display: block;
  margin-bottom: 4px;
}
.joust-scrubber-settings section .joust-scrubber-settings-checkbox span {
  margin-bottom: 0;
}
.joust-scrubber-settings section .joust-scrubber-settings-checkbox input {
  margin-top: -1px;
  margin-left: 0;
  margin-right: 4px;
