.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
@keyframes minion-intro {
  0% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes played-intro {
  0% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes draw-intro {
  0% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes damage-intro {
  0% {
    transform: scale(0.7);
  }
  50% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1);
  }
}
#container {
  width: 100%;
  height: 100%;
}
.game-wrapper {
  width: 100%;
  height: calc(100% - (35px + 8px));
  display: flex;
}
#application {
  height: 100%;
  width: 100%;
}
#application .buttons {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.5em;
  font-family: sans-serif;
  padding: 5px;
}
#application .buttons a {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
}
#application .buttons a small {
  display: inline-block;
  font-size: 0.5em;
  vertical-align: middle;
  margin: -5px 3px 0 0;
}
.game {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  font-size: 2vh;
}
.game,
#application .buttons {
  -webkit-touch-callout: none;
  user-select: none;
  cursor: default;
}
.player.current {
  background-color: #154b69;
  transition: background 0.8s, border-color 0.8s;
}
.player.inactive .short,
.player.inactive .tall {
  filter: grayscale(75%) blur(3px);
}
.player.inactive-colored .short,
.player.inactive-colored .tall {
  filter: blur(3px);
}
.entity {
  transition: box-shadow 0.15s, transform 0.15s;
}
.entity.target,
.entity.playable.target,
.entity.minion.target {
  filter: drop-shadow(0px 0px 7px #ff7800);
  transform: scale(1.05);
}
.entity.playable {
  filter: drop-shadow(0px -3px 7px #00ff00);
}
.entity.attacking {
  filter: drop-shadow(0px -3px 7px #ff0000);
  transform: scale(1.2);
}
.entity.defending {
  filter: drop-shadow(0px -3px 7px #ff0000);
}
.entity.spellTarget {
  transform: scale(1.05);
  filter: drop-shadow(0px -3px 7px #ffffff);
}
.entity.highlight {
  transform: scale(1.1);
  filter: drop-shadow(0px -3px 7px #ffffff);
}
.entity.triggered .icon-trigger {
  transition: filter 0.15s;
  filter: brightness(150%) drop-shadow(0px -3px 7px #ffff64);
}
.entity.triggered .icon-inspire {
  transition: filter 0.25s;
  filter: brightness(150%) drop-shadow(0px -3px 7px #64c8ff);
}
.entity.triggered .icon-poisonous {
  transition: filter 0.25s;
  filter: brightness(150%) drop-shadow(0px -3px 7px #64ff64);
}
.endTurnButton.playable > button {
  box-shadow: 0px 0px 10px 1px #ffff00;
}
.endTurnButton.playable.only-option > button {
  box-shadow: 0px 0px 10px 1px #00ff00;
}
.card.dragging {
  visibility: hidden;
}
.card.playable {
  filter: drop-shadow(0px 0px 7px #00ff00);
}
.card.playable.powered-up {
  filter: drop-shadow(0px 0px 7px #ffd200);
}
.card.playable.combo {
  filter: drop-shadow(0px 0px 7px #fff000);
}
.card.evil-glow {
  filter: drop-shadow(0px 0px 7px #ff6428);
}
.card.choose-both {
  filter: drop-shadow(0px 0px 7px #00ffff);
}
.card.shifting {
  opacity: 0.8;
}
.card.draggable {
  cursor: grab;
}
.choices > li {
  height: 65%;
  flex-shrink: 1;
  flex-grow: 0;
  margin: 0 1%;
}
.choices > li .card {
  z-index: 0;
  margin: 0 auto;
}
.cost,
.atk,
.health,
.durability,
.armor,
.secret-text,
.gameresult,
.damage,
.healing {
  position: absolute;
  display: block;
  width: 20%;
  text-align: center;
  color: white;
  text-shadow: 0px 0px 2px black;
  font-size: 1.2em;
  line-height: 0.95em;
  font-family: "belwe_fsextrabold", serif;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.gameresult {
  font-size: 4em;
  z-index: 101;
}
.atk.negative,
.health.negative,
.durability.negative,
.cost.negative {
  color: #dd0000;
}
.atk.positive,
.health.positive,
.durability.positive,
.cost.positive {
  color: #29dd4f;
}
.damage,
.healing {
  transform: rotate(-15deg);
}
.cost {
  color: white;
}
.stats {
  margin-top: auto;
}
.weapon,
.heroPower {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.endTurnButton {
  position: absolute;
  top: 44%;
  right: 0;
  width: 10%;
  height: 0px;
  text-align: center;
}
.endTurnButton button {
  margin: 0;
  padding: 0;
  font-size: 1em;
  position: relative;
  z-index: 1000;
  font-family: "belwe_fsextrabold", serif;
}
.no-entity {
  visibility: hidden;
}
div.visuals {
  position: relative;
}
div.visuals img {
  position: absolute;
  pointer-events: none;
}
.equipment .tray {
  font-family: "belwe_fsextrabold", serif;
  background-color: rgba(0, 0, 0, 0.3);
}
.equipment .tray > span {
  color: white;
  font-size: 0.8em;
  height: 2.5em;
  text-align: center;
  line-height: 2.5em;
  align-self: center;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.equipment .tray .crystal {
  position: relative;
  height: 1.2em;
  width: 1.2em;
  opacity: 1;
  transition: filter 400ms linear, opacity 400ms linear;
}
.equipment .tray .crystal.full {
  filter: brightness(100%);
}
.equipment .tray .crystal.empty {
  filter: brightness(50%);
}
.equipment .tray .crystal.hidden {
  opacity: 0;
}
.equipment .tray .crystal.locked {
  filter: grayscale(100%);
}
.equipment .name {
  font-family: "belwe_fsextrabold", serif;
  background-color: rgba(0, 0, 0, 0.3);
  line-height: 1.2em;
  text-align: center;
}
.joust-widget {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
  padding: 8px;
}
.game-widget {
  box-sizing: border-box;
  justify-content: space-between;
  color: white;
  background-color: #1d3657;
}
.game-widget *,
.game-widget *:before,
.game-widget *:after {
  box-sizing: inherit;
}
.joust {
  height: 100%;
  width: 100%;
}
.joust-message {
  width: 100%;
  text-align: center;
  font-family: sans-serif;
  margin: auto;
}
.deck {
  color: white;
  font-family: "belwe_fsextrabold", serif;
}
.deck.fatigue figcaption {
  color: #dd0000;
}
.deck.fatigue img {
  filter: grayscale(100%);
}
#joust-quit {
  position: absolute;
  top: 0;
  right: 0;
}
.player {
  border: solid 2px transparent;
}
.player > section:first-of-type {
  border-bottom: solid 1px #2a4e7d;
}
.player.current {
  border-color: #3ea1d9;
}
.player.current > section:first-of-type {
  border-bottom-color: #375ca7;
}
